import * as Swarms from "@hervan/swarms";
import {
  resourceStructure,
  resourceValues,
} from "@hervan/swarms/dist/Map/Terrain";
import { emojiFromSpecies } from "Board/Species";
import { COIN_EMOJI, TIME_EMOJI } from "Board/theme";
import { GAME_LENGTH } from "Swarms";
import { resourceEmojiFromTerrain } from "..";
import { colorFromTerrain, emojiFromBuilding } from "../../Map/Terrain";
import { Slot } from "./Slot";

const { TerrainList } = Swarms.Map.Terrain;
const { SpeciesList } = Swarms.Player.Species;

type Player = Swarms.Player.Player;
type TerrainMap = Swarms.Map.TerrainMap;
type Action = Swarms.Action.Action;

const { Action, ActionList } = Swarms.Action;

interface GridProps {
  map: TerrainMap;
  player: Player;
  selectedStall?: Swarms.Blueprint.Blueprint;
  selectSlot: (...args: any[]) => void;
}

const itemize = (ds: any, k?: number) =>
  Array.isArray(ds) ? (
    <ul key={k || 0} style={{ paddingInlineStart: "10px" }}>
      {Array.isArray(ds[1]) ? (
        <>
          <li key={k || 0}>{ds[0]}</li>
          {ds.slice(1).map((d, i) => itemize(d, i + 1))}
        </>
      ) : (
        ds.map((d, i) => itemize(d, i))
      )}
    </ul>
  ) : (
    <li key={k || 0}>{ds}</li>
  );

const actionDescriptions = (id: number) => [
  [
    [
      `${emojiFromSpecies(SpeciesList[id]).repeat(4)}`,
      `cost: 4  any of ${resourceStructure(id)
        .consumables.map(resourceEmojiFromTerrain)
        .join("")}`,
      `spreading ${emojiFromSpecies(
        SpeciesList[id]
      )} must be adjacent to existing ${emojiFromSpecies(SpeciesList[id])}`,
      `can't create enclosures of empty terrains surrounded by ${emojiFromSpecies(
        SpeciesList[id]
      )}`,
    ],
  ],
  [
    [
      `${emojiFromBuilding().repeat(4)}`,
      `cost: 4  any of ${resourceStructure(id)
        .consumables.map(resourceEmojiFromTerrain)
        .join("")}`,
      `drawn over ${emojiFromSpecies(SpeciesList[id]).repeat(4)}`,
      `built ${emojiFromBuilding().repeat(
        4
      )} must be adjacent to ${SpeciesList.map((s) => emojiFromSpecies(s)).join(
        ""
      )} on all sides`,
      `built ${emojiFromBuilding().repeat(
        4
      )} must touch corners (diagonally) with another of your ${emojiFromBuilding().repeat(
        4
      )}`,
      `add one new matching token to bag`,
    ],
  ],
  [
    [
      `${emojiFromSpecies(SpeciesList[id]).repeat(4)}`,
      [
        `each sale: blueprint knowledge  any one of ${resourceStructure(id)
          .consumables.map(resourceEmojiFromTerrain)
          .join("")}`,
        `amount of sales: ${SpeciesList.filter((s) => s !== SpeciesList[id])
          .map((s) => emojiFromSpecies(s))
          .join("")} (buyers) touching ${emojiFromSpecies(
          SpeciesList[id]
        ).repeat(4)} (sellers)`,
      ],
      `origin in swarm adjacent to ${emojiFromBuilding().repeat(
        4
      )} with matching blueprint, destination perpendicular to origin`,
      `reward from game: amount sold  value according to buyer`,
      [
        `if rare, each sold resource pays ${resourceStructure(id).rare}`,
        `otherwise, pays buyer's commodity resource`,
      ],
      `buyer pays game any one of ${resourceStructure(id)
        .consumables.map(resourceEmojiFromTerrain)
        .join("")} per delivered resource, keeps them`,
    ],
  ],
  [
    [
      `enable row scoring if occupied at the end of the game`,
      `there must be at least ${Swarms.NUM_PLAYERS} matching tokens available in the market to perform this action`,
      `give one token from market to each opponent to place in same row of research column`,
      `for the other players, ignore adjacency and uniqueness rules for empty spaces)`,
      `game ends after ${GAME_LENGTH} research actions are performed`,
      [
        `current player keeps playing until they're not last in turn track anymore`,
        `then other players keep playing until they reach or pass that player's final position in the turn track`,
      ],
    ],
  ],
];

const generalConcepts = (id: number) => [
  `current player is the one on top of stack most behind on turn track`,
  `resources`,
  [
    `${resourceEmojiFromTerrain(
      resourceStructure(id).commodity
    )} is your commodity resource, worth ${resourceValues(
      id,
      resourceStructure(id).commodity
    )}${COIN_EMOJI} at the end of the game`,
    `${resourceStructure(id)
      .neighbours.map(resourceEmojiFromTerrain)
      .join("")} are your neighbour resources, worth ${resourceValues(
      id,
      resourceStructure(id).neighbours[0]
    )}${COIN_EMOJI} at the end of the game`,
    `${resourceEmojiFromTerrain(
      resourceStructure(id).rare
    )} is your rare resource, worth ${resourceValues(
      id,
      resourceStructure(id).rare
    )}${COIN_EMOJI} at the end of the game`,
    `${resourceStructure(id)
      .consumables.map(resourceEmojiFromTerrain)
      .join(
        ""
      )} can be used in actions, and have total limited storage equal to the number of ${emojiFromBuilding()} in your city`,
    `you have no technology to harvest ${resourceEmojiFromTerrain(
      resourceStructure(id).rare
    )}, but you can get it through trading; you can store it in your treasury, unlimited`,
  ],
  `harvest`,
  [
    `when passing, collect ${resourceStructure(id)
      .consumables.map(resourceEmojiFromTerrain)
      .join("")} (not ${resourceEmojiFromTerrain(
      resourceStructure(id).rare
    )}) matching terrains occupied by ${emojiFromSpecies(
      SpeciesList[id]
    )} adjacent to your ${emojiFromBuilding()}`,
    `don't harvest more than your storage limit for ${resourceStructure(id)
      .consumables.map(resourceEmojiFromTerrain)
      .join("")}`,
    `draw at least one token from the bag, filling the market up to the number of each ${emojiFromBuilding().repeat(
      4
    )} in your city`,
    `for each drawn token or each resource harvested, move +${TIME_EMOJI} on turn track`,
  ],
  `actions`,
  [
    `select token from market`,
    `action locations match blueprint in token`,
    `select action space from tech grid corresponding to desired action`,
    [
      `if not empty, tokens must match`,
      `if empty, restrictions below must be satisfied`,
      `otherwise the action is not allowed`,
    ],
    `return token to bag after performing action`,
  ],
  `empty action spaces`,
  [
    `add new token to space`,
    `it must be unique in both row and column`,
    `space must be adjacent to occupied space`,
  ],
];

export const Grid = ({ map, player, selectedStall, selectSlot }: GridProps) => (
  <>
    {player.grid.map((row, i) => (
      <tr key={`gridrow${i}`}>
        {ActionList.map((action, col) => (
          <td
            key={`${i}x${col}`}
            style={{
              border: "0.1vmin solid #555",
              backgroundColor:
                action === Action.Trade
                  ? colorFromTerrain(
                      TerrainList[(i + player.id) % ActionList.length]
                    )
                  : "",
              cursor: "pointer",
              height: "20vmin",
              verticalAlign: "top",
              fontSize: "1.4vmin",
            }}
            onClick={() => selectSlot(i, action)}
          >
            <Slot
              blueprint={
                i === player.selection.slot?.row &&
                action === player.selection.slot.action
                  ? player.selection.stall === undefined
                    ? row[action].blueprint
                    : selectedStall
                  : row[action].blueprint
              }
              action={action}
              row={i}
              player={player}
              map={map}
              selected={
                i === player.selection.slot?.row &&
                action === player.selection.slot.action
              }
            />
          </td>
        ))}
        {i === 0 ? (
          <td
            style={{
              border: "0.1vmin solid #555",
              padding: "1vmin",
              verticalAlign: "top",
              fontSize: "1.4vmin",
              width: "20vmin",
            }}
            rowSpan={5}
          >
            <span style={{ fontWeight: "bold" }}>general game concepts</span>
            {itemize(generalConcepts(player.id))}
          </td>
        ) : undefined}
      </tr>
    ))}
    <tr>
      {actionDescriptions(player.id).map((d, i) => (
        <td
          key={i}
          style={{
            border: "0.1vmin solid #555",
            fontSize: "12px",
            overflow: "visible",
            overflowWrap: "break-word",
            wordWrap: "break-word",
            verticalAlign: "top",
          }}
        >
          {itemize(d)}
        </td>
      ))}
    </tr>
  </>
);
