import { Player as PlayerModule } from "@hervan/swarms";
import type { BoardProps } from "boardgame.io/react";
import { useState } from "react";
import { type SwarmsState } from "../Swarms";
import { Map } from "./Map";
import { Market } from "./Market";
import { Player } from "./Player";
import { TurnTrack } from "./TimeTrack";

const winner = PlayerModule.winner;

export interface GameProps extends BoardProps<SwarmsState> {}

export const Board = (props: GameProps) => {
  const id = parseInt(props.playerID || "-1");
  const [showAll, setShowAll] = useState(id < 0);
  const displayPlayer = props.G.players[id < 0 ? 0 : id];
  return (
    <>
      <Map
        map={props.G.map}
        selection={displayPlayer.selection}
        deselectLocation={props.moves.deselectLocation}
        deselectDestination={props.moves.deselectDestination}
        selectLocation={props.moves.selectLocation}
        selectDestination={props.moves.selectDestination}
      />
      <TurnTrack players={props.G.players} track={props.G.turnTrack} />
      <Market
        market={props.G.market}
        selectedStall={displayPlayer.selection.stall}
        draftToken={props.moves.draftToken}
      />
      {!showAll ? (
        <Player
          map={props.G.map}
          player={displayPlayer}
          selectedStall={
            displayPlayer.selection.stall === undefined
              ? undefined
              : props.G.market.stalls[displayPlayer.selection.stall]
          }
          selectSlot={props.moves.selectSlot}
          winner={
            props.ctx.gameover &&
            winner(props.G.players, props.G.map).id === displayPlayer.id
          }
          isActive={props.isActive}
          passTurn={props.moves.passTurn}
          discardSelection={props.moves.discardSelection}
          showAll={showAll}
          setShowAll={setShowAll}
        />
      ) : (
        props.G.players.map((player, i) => (
          <Player
            key={i}
            map={props.G.map}
            selectSlot={props.moves.selectSlot}
            player={player}
            winner={
              props.ctx.gameover &&
              winner(props.G.players, props.G.map).id === player.id
            }
            isActive={props.isActive}
            passTurn={props.moves.passTurn}
            discardSelection={props.moves.discardSelection}
            showAll={showAll}
            setShowAll={setShowAll}
          />
        ))
      )}
    </>
  );
};
